import { mapState } from "vuex";

export default {
    name: 'app-condition',
    props: {
        check: {
            type: Object
        },
        read: {
            type: Boolean,
            default() {
                return false
            }
        },
        setting: {
            type: Object
        },
        province: {
            type: Object
        },
        city: {
            type: Object
        },
        district: {
            type: Array
        },
        rate: {
            type: Number,
            default() {
                return 0;
            }
        }
    },
    data() {
        return {
            beApply: false,
        }
    },
    computed: {
        ...mapState({
            region: state => state.mallConfig.__wxapp_img.region,
        })
    },
    methods: {
        toApply() {
            let that = this;
            if(that.setting.apply_type == 2) {
                that.$emit('submit');
            }else {
                that.beApply = true;
                that.$emit("update",that.beApply);
            }
        },

        resetChoose() {
            this.$emit("reset",false);
        },

        toggle() {
            this.$emit("beRead");
        },

        toRead() {
            let that = this;
            that.$emit("toRead");
        },

        toIndex() {
            uni.reLaunch({
                url: '/pages/index/index'
            });
        },
    }
}
